@import '../../sass/utils/mixin';

.sectionGoods {
  padding-top: 32px;
  padding-bottom: 64px;
  background-color: var(--whiteBG);
}

.sectionGoodsTitle {
  font-family: 'Montserrat SemiBold';
  font-size: 24px;
  line-height: 1.4;
  text-align: start;
  color: var(--richSoilBrown);
  padding: 8px;
  margin-bottom: 8px;
}

.sectionGoodsBorder {
  border-bottom: 1px solid var(--copperYellow);
  margin-left: -16px;
  margin-bottom: 16px;
}

.sectionGoodsCategoryBtn {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  color: var(--eggShellWhite);
  background-color: var(--richSoilBrown);
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
  margin-bottom: 8px;
  min-height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  text-align: start;
}

.toggleBtnImg {
  width: 40px;
  height: 40px;
}

.sectionGoodsCategoryList {
  padding-left: 14px;
  padding-right: 14px;
  margin-bottom: 16px;
}

.sectionGoodsCategoryList__item {
  display: flex;
  align-items: center;
  padding: 8px 15px;
  border-radius: 4px;
  background-color: var(--brownBG);
  box-shadow: 2px 2px 15px 4px rgba(107, 103, 102, 0.15);
  margin-bottom: 4px;
  min-height: 48px;
  width: 100%;
}

.sectionGoodsCategoryList__item:last-child {
  margin-bottom: 0px;
}

.sectionGoodsCategoryList__itemName {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  color: var(--eggShellWhite);
  width: 100%;
}

.findCountContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}

.findTitle {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  color: var(--grayColor);
  padding: 8px;
}

.borderforGoodsCategoryList {
  border-bottom: 1px solid var(--grayColor);
  margin-bottom: 16px;
}

.findNumerCount {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  color: var(--copperYellow);
  padding: 8px;
}

.findNumerContainer {
  display: flex;
  align-items: center;
}

.productList {
  padding-top: 32px;
}

.productListItem {
  width: 100%;
  min-height: 136px;
}

.productListItemTitle {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  color: var(--richSoilBrown);
}

.productListItemCategoryNone {
  display: none;
}

.productListItemDescription {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  color: var(--richSoilBrown);
  margin-bottom: 4px;
}

.productListItemManufacturer {
  font-family: 'Montserrat Regular';
  font-size: 14px;
  line-height: 1.44;
  color: var(--richSoilBrown);
  margin-bottom: 4px;
}

.productListItemImg {
  display: none;
}

.findDescription {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  color: var(--richSoilBrown);
  padding: 8px 8px 8px 0px;
}

.productListItem {
  margin-bottom: 24px;
  border-radius: 4px;
  box-shadow: 2px 2px 15px 4px rgba(107, 103, 102, 0.15);
}

.productListItem {
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
  background-color: var(--eggShellWhite);
  flex-direction: column;
  padding: 4px 8px;
}

.selectedCategory,
.selectedCategoryText {
  color: var(--richSoilBrown) !important;
  background-color: var(--copperYellow);
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
}

.sectionGoodsCategoryList__itemImg {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 32px;
  margin-right: 4px;
}

.categoryIcon {
  color: var(--brownBG);
}

.selectedIcon,
.iconManufacturer {
  filter: brightness(0) invert(0);
}

.noResults {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  color: var(--richSoilBrown);
  text-align: center;
}

.customCheckbox {
  width: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;

  input {
    display: none;
  }

  .checkmark {
    width: 21px;
    height: 18px;
    border-radius: 2px;
    border: 2px solid var(--eggShellWhite);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    background-color: var(--grayColor);
  }

  .checkIcon {
    width: 12px;
    height: 9.4px;
  }

  input:checked + .checkmark {
    background-color: var(--richSoilBrown);
    border-color: var(--richSoilBrown);
    border: none;
  }
}

.selectedCategory {
  background-color: var(--copperYellow) !important;
}

.sectionGoodsCategoryList__itemNameSpan {
  font-family: 'Montserrat Regular';
  font-size: 14px;
  line-height: 1.43;
}

.iconManufacturerContainerBorder {
  border-right: 1px solid var(--copperYellow);
  margin-right: 4px;
}

.iconManufacturerContainerBorder:last-child {
  margin-right: 0px;
}

.iconManufacturerContainer {
  display: flex;
  align-items: center;
}

.centeredText {
  text-align: center;
  padding-right: 34px;
}
